                            <div class="project-bg"
                                style="background-image: url('https://previews.123rf.com/images/iunewind/iunewind1505/iunewind150500146/40225186-html-code.jpg')">
                            </div>
                        </div>

                        <div class="project">
                            <div class="project-front">
                                <span class="project-name"> Geoloc </span>
                                <span class="project-tag">
                                    <span class="badge badge-secondary">C</span>
                                    <span class="badge badge-secondary">SFML</span>
                                    <span class="badge badge-secondary">Machine Learning</span>
                                </span>
                            </div>
                            <div class="project-bg" style="background-image: url('static/img/C.PNG')">
                            </div>
                        </div>

                        <div class="project">
                            <div class="project-front">
                                <span class="project-name"> Mask R-Cnn + Trackers </span>
                                <span class="project-tag">
                                    <span class="badge badge-secondary">Python</span>
