                </div>
                <div class="row justify-content-center">
                    <a class="text-white text-center" href="https://github.com/Makunda\">
                        <h3><i>See more projects on my Github @Makunda</i></h3>
                    </a>
                </div>
            </div>
        </transition>
        <transition name="fade">
            <div id="project_focus" v-if="focusedProject !== null">
                <div class="row justify-content-center">
                    <div class="col-lg-4" id="project_side_list">
                        <div class="project" v-for="project in registeredProjects" :key="project"
                            v-on:click="focusedProject = project">
                            <div class="project-front">
                                <span class="project-name"> {{ project.title }} </span>
                                <div class="project-tag">
                                    <span class="badge badge-secondary mr-1" v-for="tag in project.tags" :key="tag"> {{tag}}
                                    </span>
                                </div>
                            </div>

                            <div class="project-bg"
                                :style="{ 'background-image': 'url(' + project.background_image + ')' }">
                            </div>
                        </div>
                    </div>
                    <div class="col-lg-7 d-flex" id="project_description">
                        <div class="container-fluid">
                            <span class="close-project text-danger" v-on:click="focusedProject = null"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="30" height="30"><path fill-rule="evenodd" d="M1 12C1 5.925 5.925 1 12 1s11 4.925 11 11-4.925 11-11 11S1 18.075 1 12zm8.036-4.024a.75.75 0 00-1.06 1.06L10.939 12l-2.963 2.963a.75.75 0 101.06 1.06L12 13.06l2.963 2.964a.75.75 0 001.061-1.06L13.061 12l2.963-2.964a.75.75 0 10-1.06-1.06L12 10.939 9.036 7.976z"></path></svg></span>
