                            <div class="row d-flex m-1">
                                <h3>Tags :  <span class="badge badge-secondary m-1" v-for="tag in focusedProject.tags" :key="tag"> {{tag}}</span></h3>
                            </div>
                            <div class="row m-1">
                                <p>
                                    <span v-if="focusedProject.start_date"> <u  class="m-1">Project start : </u> {{ focusedProject.start_date }}</span>
                                <br>
                                <span v-if="focusedProject.end_date"><u  class="m-1">Project end : </u> {{ focusedProject.end_date }}</span>
                                </p>
                            </div>
                            <div class="row m-1">
                                <p class="m-1">Description : <span class="m-1" v-html="focusedProject.description"></span></p>
                            </div>
                            <div class="row m-1">
                                <p class="m-1" v-html="focusedProject.body"></p>
                            </div>
                        </div>
                    </div>
