            </div>
        </transition>
    </div>
</template>

<style>

    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to {
        opacity: 0;
        transition: opacity .5s;
    }

    .close-project {
        fill: tomato;
        color: tomato;
        position: absolute;
        top: 0;
        right: 0;
        text-align: center;
        margin: 8px;
        height: 30px;
        width: 30px;
    }

    #project_focus {
        padding-top: 70px;
    }

    #project_side_list {
        display: flex;
        flex-direction: column;
        padding: 40px;
        height: max-content;
    }

    #project_side_list .project{
        min-height: 120px;
    }

    #project_side_list .project-front:hover{
        background-color: rgba(0, 0, 0, 0.15);
        transition-duration: 1s;
    }

    #project_description {
        position: relative;
        border-radius: 10px;
        background-color: rgba(0, 0, 0, 0.15);
        padding: 20px;
        margin-top: 40px;
        height: max-content;
        max-height: 700px;
        overflow-y: auto;
    }

    .project-container {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -ms-flex-direction: row;
        flex-direction: row;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
    }

    .project {
        position: relative;
        border-radius: 0.5rem;
        height: 150px;
        background-color: black;
        margin: 5px;
        -webkit-box-flex: 1;
        -ms-flex: 1 1 45%;
        flex: 1 1 45%;
        overflow: hidden;
    }

    .project-bg {
        position: absolute;
        z-index: 100;
        width: 100%;
        height: 100%;
        background-color: black;
        /* fallback color */
        background-position: center;
        background-size: cover;
        top: 0;
        -o-transition-duration: 1s;
        transition-duration: 1s;
        -moz-transition-duration: 1s;
        -webkit-transition-duration: 1s;
    }

    .project-front {
        position: relative;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -ms-flex-direction: row;
        flex-direction: row;
        z-index: 101;
        width: 100%;
        height: 100%;
        color: white !important;
        background-color: rgba(0, 0, 0, 0.35);
        /* fallback color */
        background-position: center;
        background-size: cover;
    }

    .project-front .project-name {
        height: auto;
        font-size: 3rem;
        padding: 10px;
    }

    .project-front .project-tag {
        position: absolute;
        padding: 5px;
        bottom: 0;
        right: 0;
        font-size: 2rem;
    }

    .project-front .project-tag .badge {
        background-color: #343A40;
    }


    .project:hover .project-bg,
    .parent:focus .project-bg {
        -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
        transform: scale(1.2);
        -o-transition-duration: 1s;
        transition-duration: 1s;
        -moz-transition-duration: 1s;
        -webkit-transition-duration: 1s;
    }
</style>

<script>

    let opacity = 0;
    let ticking = false;
    function darkBackground() {


        if (!ticking) {
            window.requestAnimationFrame(function () {
                darkBackground();
                ticking = false;
            });

            ticking = true;
        }

        var bounding = document.getElementById('project_section').getBoundingClientRect();
        if (bounding.y > 0 && bounding.y < 1000) {

            opacity = 1 - (bounding.y) / 1000;
            document.getElementById('project_section').style.backgroundColor = `rgba(52, 58, 64,${opacity})`;
        } else {
            document.getElementById('project_section').style.backgroundColor = `rgba(52, 58, 64,1)`;
            window.removeEventListener('scroll', darkBackground, false);
            return 1;
        }

    }



    import Moirai from "../assets/projects/Moirai.json";
    import TradingBot from "../assets/projects/TradingBot.json";

    export default {
        name: 'ProjectSection',
        methods: {

        },
        mounted() {
            darkBackground();
            window.addEventListener('scroll', darkBackground, false);
        },
        props: {

        },
        data() {
            return {
                focusedProject: null,
                registeredProjects: { moirai: Moirai, tradingBot: TradingBot }
            }
        }
    }
</script>